import React from 'react';
import styled from 'styled-components';
import {Link} from 'components';

const Wrapper = styled.div `
padding: 15px 20px;
border:1px solid white;
color: white;
font-weight: bold;
font-size: 25px;
margin-left: 30px;
&.header{
  border-bottom: 0px;
}
&.separator{
  color: transparent;
}
`
class Merge extends React.Component {

  constructor(props) {
    super(props);
  }

  render() {

    return (
      <Wrapper className={(this.props.title ? 'header': '')}>
        {this.props.title}
      </Wrapper>
    )
  }
}

export default Merge;
